html {
  font-size: 62.5%;
}
body {
  font-size: 1.6rem;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Inter Regular", "Arial", "Tahoma", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
  // background: #D2D2D2;
}

header,
section,
footer,
main {
  width: 100%;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

.wrapper {
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 100%;
  max-width: calc(113rem + 3rem); // Максимальная ширина контейнера с учетом паддингов (2 х 1.5rem)
  padding: 0 1.5rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

// Запрет скролла
.no-scroll {
  overflow: hidden;

  @media (min-width: 1024px) {
    padding-right: 1.7rem;
  }
}

