:root {
  /* COLORS */
  --red_1: #BF0902;
  --black_1: #120600;
  --black_2: #000000;
  --gray_1: #757575;
  --gray_2: #666666;
  --gray_3: #DEDEDE;
  --gray_4: #D9D9D9;
  --gray_5: #454545;
  --gray_6: #9A9A9A;
  --gray_7: #535353;
  --white_1: #FFFFFF;
  --white_2: #F5F5F5;
  --white_3: #ADADAD;
  /* FONTS-SIZES */
}

/* FONTS */
/* Inter */
*,
*:before,
*:after {
  box-sizing: border-box;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select,
  textarea,
  input {
    font-size: 1.6rem !important;
  }
}
img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

input[type=submit],
button {
  cursor: pointer;
}

hr {
  display: block;
  height: 0.1em;
  border: 0;
  border-top: 0.1em solid #ccc;
  margin: 1em 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: "Inter Regular", "Arial", "Tahoma", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
}

header,
section,
footer,
main {
  width: 100%;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

.wrapper {
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 100%;
  max-width: 116rem;
  padding: 0 1.5rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.no-scroll {
  overflow: hidden;
}
@media (min-width: 1024px) {
  .no-scroll {
    padding-right: 1.7rem;
  }
}

.red {
  color: var(--red_1);
}

h1 {
  font-family: "Inter Bold";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  font-size: 6.8rem;
  line-height: 110%;
  text-align: center;
  letter-spacing: -0.035em;
  color: var(--black_1);
}

h2 {
  font-family: "Inter SemiBold";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  font-size: 4rem;
  line-height: 120.02%;
  color: var(--black_1);
}

.btn {
  display: flex;
  align-items: center;
  text-align: center;
  padding: 1.25rem 2.885rem;
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  outline: none;
  border: none;
  border-radius: 5rem;
}
.btn--black {
  color: var(--white_1);
  background: var(--black_1);
}
.btn--red {
  color: var(--white_1);
  background: var(--red_1);
}

.header__inner {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.header__inner-top {
  padding: 2rem 0;
  border-bottom: 0.1rem solid var(--gray_1);
}
.header__inner-top, .header__inner-bottom {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__btn {
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: var(--black_1);
  border: 0.1rem solid var(--red_1);
  background: transparent;
}

.footer {
  padding: 8rem 0 2.9rem 0;
  background: var(--black_1);
}
.footer__inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.footer__inner-left .logo {
  margin-bottom: 7.8rem;
}
.footer__inner-left nav {
  max-width: 77rem;
  margin-bottom: 8.4rem;
}
.footer__inner-left nav ul {
  display: flex;
  flex-wrap: wrap;
}
.footer__inner-left nav ul li {
  width: 18%;
  margin-right: 5rem;
}
.footer__inner-left nav ul li:nth-of-type(1), .footer__inner-left nav ul li:nth-of-type(2), .footer__inner-left nav ul li:nth-of-type(3), .footer__inner-left nav ul li:nth-of-type(4) {
  margin-bottom: 2rem;
}
.footer__inner-left nav ul li a {
  color: var(--gray_6);
  will-change: color;
  transition: color 0.3s ease-in-out;
}
.footer__inner-left nav ul li a:hover, .footer__inner-left nav ul li a:focus {
  color: var(--red_1);
}
.footer__inner-right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.footer__inner-right .phone {
  margin-bottom: 8.1rem;
}
.footer__inner-right span {
  display: inline-block;
  margin-bottom: 1rem;
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: var(--white_1);
}
.footer__inner-btn {
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: var(--red_1);
  background: transparent;
  border: 0.1rem solid var(--red_1);
}
.footer .copyright {
  width: 100%;
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: var(--gray_7);
}

.nav {
  width: 100%;
  padding: 2rem 0;
}
.nav ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav ul li a {
  padding: 0.5rem;
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: var(--black_1);
}

.card {
  width: 26rem;
  padding: 2rem 1.5rem 2rem 2rem;
  border: 0.1rem solid var(--gray_3);
  border-radius: 0.5rem;
}
.card__top {
  align-items: center;
  margin-bottom: 1rem;
}
.card__top span {
  font-family: "Inter Medium";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  font-size: 2.6rem;
  line-height: 3.1rem;
  color: var(--black_1);
}
.card__top, .card__bottom {
  display: flex;
  justify-content: space-between;
}

.phone a {
  font-family: "Inter Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  font-size: 1.4rem;
  line-height: 1.7rem;
  color: var(--white_1);
}

.landscaping {
